<template>
  <div class="hospitalCard">
    <div class="level">
      <span class="text">等级</span>
      <span class="content" v-if="hospitalDetails.hospitalLevel==='1'">三甲</span>
    </div>
    <div class="category">
      <span class="text">性质</span>
      <span class="content" v-if="hospitalDetails.hospitalType==='1'">公立</span>
    </div>
    <div class="area">
      <span class="text">区域</span>
      <span class="content">{{hospitalDetails.address | addressFilter}}</span>
  </div>
  </div>
</template>

<script>
export default {
  name: 'hospitalCard',
  props: ['hospitalDetails'],
  filters: {
    addressFilter (str) {
      const reg = /(\S+市)\S+$/
      return str.match(reg)[1]
    }
  }
}
</script>

<style scoped lang="less">
  .hospitalCard {
    width: 3.45*2rem;
    height: .72*2rem;
    background: #fff;
    box-shadow: 0.03*2rem 0.03*2rem 0.1*2rem 0.05*2rem rgba(51,145,232,0.10);
    border-radius: 0.04*2rem;
    display: flex;
    padding: .16*2rem 0 .15*2rem 0;
    box-sizing: border-box;
    div{
      flex: 1;
      text-align: center;
      border-right: 1px solid #F6F7F8;
      span{
        display: block;
      }
      .text{
        font-family: PingFangSC-Regular,sans-serif;
        color: #272727;
        font-size: .11*2rem;
        line-height: .16*2rem;
        opacity: .6;
      }
      .content{
        margin-top: .05*2rem;
        font-family: PingFangSC-Regular,sans-serif;
        color: #272727;
        font-size: .14*2rem;
        line-height: .2*2rem;
      }
      &:nth-last-of-type(1){
        border-right: none;
      }
    }
  }
</style>



