<template>
  <div class="back" @click="back">
返回
  </div>
</template>

<script>
  export default {
    name: 'back',
    methods: {
      back () {
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped lang="less">
  .back {
    position: absolute;
    background: url('../../../assets/header/nav_back@2x.png') no-repeat .15*2rem center;
    background-size: 0.06*2rem 0.12*2rem;
    /*line-height: .44*2rem;*/
    line-height:normal;
    padding: 0.12*2rem 0;
    font-size: 0.14*2rem;
    text-indent: 0.3*2rem;
    z-index: 10;
    color: #fff;
  }
</style>



