<template>
  <div class="detailBox">
    <div class="title">医院详情</div>
    <div class="detail">
      <div class="phone">
        <span class="text">联系电话：</span>
        <span class="content">{{hospitalDetails.telephone | telFilter}}</span>
      </div>
      <div class="webSite">
        <span class="text">医院网址：</span>
        <span class="content"><a href="http://www.heze.com">http://www.heze.com</a></span>
      </div>
      <div class="introduce">
        <span class="text">医院简介：</span>
        <span class="content">菏泽市第三人民医院（菏泽市精神卫生中心），始建于1971年1月，初为菏泽地区精神病医院，1985年向“大专科小综合”方面发展，1992年更名为菏泽地区第三人民医院（菏泽地区精神卫生中心），2000年撤地设市更为菏泽市第三人民医院（菏泽市精神卫生中心）</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detail',
  props: ['hospitalDetails'],
  filters: {
    telFilter (str) {
      const reg = /\((\d+)\)(\d+)/
      return str.match(reg)[1] + '-' + str.match(reg)[2]
    }
  }
}
</script>

<style scoped lang="less">
  .detailBox {
    padding: 0 0.14*2rem 0 .15*2rem;

    .title {
      font-family: PingFangSC-Medium, sans-serif;
      color: #272727;
      font-size: .16*2rem;
      line-height: .22*2rem;
    }

    .detail {
      margin-top: .2*2rem;

      span {
        font-family: PingFangSC-Regular, sans-serif;
        font-size: .14*2rem;
        line-height: .2*2rem;
        color: #272727;
      }

      .phone {
        display: flex;

        .content {
          flex: 1;
        }
      }

      .webSite {
        display: flex;
        margin-top: 0.06*2rem;

        .content {
          flex: 1;

          a {
            color: #3391E8;
          }
        }
      }

      .introduce {
        margin-top: 0.07*2rem;
        display: flex;

        .content {
          flex: 1;
        }
      }
    }
  }
</style>



