<template>
  <div class="hospitalDetails">
    <div class="title">
      <Back/>
      <img src="../../../assets/appointment/hospital_bg@2x.png" alt="" class="bgImg">
      <div class="mask"></div>
      <span class="text">{{hospitalDetails.hospitalName}}</span>
    </div>
    <HospitalCard :hospitalDetails="hospitalDetails"/>
    <Detail :hospitalDetails="hospitalDetails"/>
  </div>
</template>

<script>
import Back from './back'
import HospitalCard from './hospitalCard'
import Detail from './detail'
import store from '@/vuex/index'
export default {
  name: 'hospitalDetails',
  components: {
    Back,
    HospitalCard,
    Detail
  },
  computed: {
    hospitalDetails () {
      return store.state.wmodule.curHospitalDetails
    }
  },
  methods: {},
  created () {
    // console.log(this.$route.params)
    const {hospitalId} = this.$route.params
    store.dispatch('wmodule/getHospitalInfo', hospitalId)
  }
}
</script>

<style scoped lang="less">
  .hospitalDetails {
    position: relative;
    top: -.44*2rem;
    .title {
      position: relative;
      z-index: 100;
      width: 100%;
      height: 1.8*2rem;

      .back {
        /*margin-left: .15*2rem;*/
        /*margin-top: .12*2rem;*/
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
      }

      .mask {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: #3391e8;
        opacity: 0.8;
      }

      .text {
        font-family: PingFangSC-Medium, sans-serif;
        font-size: .2*2rem;
        line-height: .28*2rem;
        position: absolute;
        z-index: 10;
        color: #fff;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0.56*2rem;
      }
    }
    .hospitalCard{
      position: absolute;
      left: 50%;
      margin-left: -1.725*2rem;
      transform: translateY(-50%);
      z-index: 100;
    }
    .detailBox{
      margin-top: .61*2rem;
    }
  }
</style>



